<template>
	<div class="o-Loading">
		<div class="u-center o-Loading__spin">
			<svg viewBox="25 25 50 50">
				<circle class="path" cx="50" cy="50" r="20" fill="none"/>
			</svg>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Loading'
	}
</script>



